<template>
  <v-row>
    <v-col cols="12" md="12" xl="8">
      <v-card class="rounded-lg" width="100%" flat>
        <v-sheet class="d-flex" color="secondary">
          <v-container class="my-container pa-0" fluid>
            <v-overlay :value="overlay" opacity="0.36" absolute>
              <v-row justify="center">
                <v-progress-circular
                  size="100"
                  color="primary"
                  indeterminate
                ></v-progress-circular>
              </v-row>
            </v-overlay>
            <v-card-title></v-card-title>
            <v-card-text class="px-0">
              <v-stepper v-model="step" class="my-stepper" flat>
                <v-row class="d-flex justify-center">
                  <v-col cols="12" md="8">
                    <v-stepper-header>
                      <v-stepper-step :complete="step > 1" step="1">
                        閱讀說明
                      </v-stepper-step>
                      <v-divider></v-divider>
                      <v-stepper-step :complete="step > 2" step="2">
                        填寫問卷
                      </v-stepper-step>
                      <v-divider></v-divider>
                      <v-stepper-step step="3"> 取得報告 </v-stepper-step>
                    </v-stepper-header>
                  </v-col>
                </v-row>

                <v-stepper-items>
                  <v-stepper-content step="1" class="px-2">
                    <v-row class="d-flex justify-center">
                      <v-col cols="12" md="8">
                        <Introduction v-on:next="toForm" />
                      </v-col>
                    </v-row>
                  </v-stepper-content>

                  <v-stepper-content step="2">
                    <v-row class="d-flex justify-center">
                      <v-col cols="12" md="11">
                        <Form
                          ref="form"
                          v-on:next="toReport"
                          v-on:prev="prevStep"
                        />
                      </v-col>
                    </v-row>
                  </v-stepper-content>

                  <v-stepper-content step="3">
                    <v-row class="d-flex justify-center">
                      <v-col cols="12" md="8">
                        <Report
                          v-on:prev="prevStep"
                          :now="now"
                          :dateDiff="dateDiff"
                          :jobTitle="jobTitle"
                          :dateRangeText="dateRangeText"
                        />
                      </v-col>
                    </v-row>
                  </v-stepper-content>
                </v-stepper-items>
              </v-stepper>
            </v-card-text>
            <v-fab-transition>
              <v-btn
                v-scroll="onScroll"
                v-show="showGoToTop"
                color="primary"
                @click="toTop"
                class="my-btn"
                small
                fab
                dark
                fixed
                bottom
                right
              >
                <v-icon>mdi-chevron-up</v-icon>
              </v-btn>
            </v-fab-transition>
          </v-container>
        </v-sheet>
      </v-card>
    </v-col>
  </v-row>
</template>

<script>
import moment from "moment";
import Introduction from "@/components/daily-compliance/recruiting-and-hiring/manager-and-LSA-applicability/Introduction.vue";
import Form from "@/components/daily-compliance/recruiting-and-hiring/manager-and-LSA-applicability/Form.vue";
import Report from "@/components/daily-compliance/recruiting-and-hiring/manager-and-LSA-applicability/Report.vue";
import { createInvoice } from "@/hooks/api.js";

export default {
  name: "",
  components: { Introduction, Form, Report },
  data: () => ({
    overlay: false,
    offsetTop: 0,
    step: 1,
    now: "",
    jobTitle: "",
    dateDiff: 0,
    dateRangeText: "",
  }),
  // watch: {
  //   step: function () {
  //     this.$refs.form.$refs.form.reset();
  //   },
  // },
  computed: {
    showGoToTop() {
      return this.offsetTop > 50;
    },
  },
  methods: {
    onScroll(event) {
      this.offsetTop = event.target.scrollingElement.scrollTop;
    },
    toTop() {
      this.$vuetify.goTo(0);
    },
    prevStep(step) {
      this.$vuetify.goTo(0);
      setTimeout(() => {
        this.step = step;
      }, 500);
    },
    toForm() {
      this.step = 2;
    },
    toReport(params) {
      this.$vuetify.goTo(0);
      setTimeout(() => {
        this.now = moment(new Date()).format("YYYY-MM-DD HH:mm");
        this.jobTitle = params.jobTitle;
        this.step = 3;
      }, 500);
    },
    close() {
      this.active = [0];
      this.$emit("input", false);
    },
    getActiveValue(value) {
      this.selected = value[0];
      console.log(value);
    },
    hideAlert() {
      window.setInterval(() => {
        this.alert = false;
      }, 3000);
    },
    submit() {
      if (this.$refs.form.validate()) {
        createInvoice({
          merchant_trade_no: this.merchant_trade_no,
        })
          .then(() => {
            this.alertType = "success";
            this.alertText = "開立成功";
          })
          .catch((err) => {
            this.alertType = "warning";
            const code = err.response.data.code;
            switch (code) {
              case "09002":
                this.alertText = "此訂單已開立過發票";
                break;
              case "09003":
                this.alertText = "訂單號碼不存在";
                break;
              default:
                this.alertText = "開立失敗";
            }
          });
        this.alert = true;
        this.$refs.form.reset();
        this.hideAlert();
      }
    },
  },
};
</script>

<style scoped>
.my-btn {
  bottom: 106px;
  right: 46px;
  z-index: 99;
}

.my-stepper {
  background: transparent !important;
}

.my-stepper .v-stepper__header {
  box-shadow: unset;
}

.my-container {
  color: #eeeeee;
  background: rgba(255, 255, 255, 0.12);
  border-radius: 8px;
}
