<script>
export default {
  name: "",
  components: {},
  data: () => ({
    valid: true,
    rules: [(v) => !!v || ""],
    step: 2,
    activePicker: null,
    date: null,
    dateRangeText: "",
    dateDiff: 0,
    menu: false,
    jobTitle: "",
    jobDone: null,
    question: [
      {
        id: "q1",
        q: "任職者到職前，是否須先經貴單位董事會決議 (股份有限公司) 或股東同意 (有限公司) ？",
      },
      {
        id: "q2",
        q: "任職者是否透過貴單位、關係企業或集團的內部升遷管道而擔任現職？",
      },
      { id: "q3", q: "任職者可否自由決定上下班時間？" },
      { id: "q4", q: "任職者請假是否須經貴單位同意或核准？" },
      { id: "q5", q: "任職者可否自由決定上班或執行工作的地點？" },
      {
        id: "q6",
        q: "針對職務工作事項，任職者對內是否具有決策或決定的權限？",
      },
      {
        id: "q7",
        q: "針對職務工作事項，任職者對外是否有權代表貴單位簽訂契約？",
      },
    ],
    q1: null,
  }),
  watch: {
    menu(val) {
      val && setTimeout(() => (this.activePicker = "YEAR"));
    },
  },
  methods: {
    save(date) {
      const start = new Date(this.date[0]);
      const end = new Date(this.date[1]);
      this.$refs.menu.save(date);
      this.dateDiff = parseInt(Math.abs(end - start) / 1000 / 60 / 60 / 24);
      this.dateRangeText = date.join(" ～ ");
    },
    clear() {
      this.date = null;
      this.dateDiff = 0;
      this.dateRangeText = "";
    },
    next() {
      if (this.$refs.form.validate()) {
        const params = {
          jobTitle: this.jobTitle.trim(),
          dateDiff: this.dateDiff,
          dateRangeText: this.dateRangeText,
        };
        this.$emit("next", params);
      } else {
        this.$nextTick(() => {
          const el = this.$el.querySelector(
            ".v-input--has-state.error--text:first-of-type"
          );
          this.$vuetify.goTo(el);
          return;
        });
      }
    },
    prev() {
      this.$emit("prev", 1);
    },
  },
};
</script>

